      showLabel: false,
      cityLabel: '',
      showLabel: false, 
      newLabel: "", 
    }
  },
  props: {
    favoriteCities: Array,
  },
  methods: {
    removeCity: function (city) {
      let cityIndex= this.favoriteCities.indexOf(city);
